.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
}

.modal-container {
  background-color: white;
  border-radius: 0.75rem;
  box-shadow: 0 25px 50px -12px rgba(0, 0, 0, 0.25);
  width: 100%;
  max-width: 700px;
  max-height: 90vh;
  overflow-y: auto;
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.modal-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 1.5rem 1rem 1.5rem;
  border-bottom: 1px solid #e5e7eb;
}
.modal-header .modal-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: #111827;
  margin: 0;
}
.modal-header .close-btn {
  background: none;
  border: none;
  color: #6b7280;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 0.375rem;
  transition: all 0.2s;
}
.modal-header .close-btn:hover {
  background-color: #f3f4f6;
  color: #374151;
}
.modal-header .close-btn svg {
  width: 1.25rem;
  height: 1.25rem;
}

.modal-body {
  padding: 1.5rem;
}
.modal-body .error-message {
  background-color: #fef2f2;
  border: 1px solid #fecaca;
  color: #dc2626;
  padding: 1rem;
  border-radius: 0.5rem;
  margin-bottom: 1.5rem;
}
.modal-body .po-summary {
  margin-bottom: 2rem;
  padding: 1rem;
  background-color: #f9fafb;
  border-radius: 0.5rem;
  border: 1px solid #e5e7eb;
}
.modal-body .po-summary .section-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #374151;
  margin: 0 0 1rem 0;
}
.modal-body .po-summary .summary-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}
.modal-body .po-summary .summary-grid .summary-item label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  color: #6b7280;
  margin-bottom: 0.25rem;
}
.modal-body .po-summary .summary-grid .summary-item span {
  display: block;
  font-size: 1rem;
  color: #111827;
  font-weight: 500;
}
.modal-body .receive-form .form-section {
  margin-bottom: 2rem;
}
.modal-body .receive-form .form-section:last-child {
  margin-bottom: 0;
}
.modal-body .receive-form .form-section .section-title {
  font-size: 1.125rem;
  font-weight: 600;
  color: #374151;
  margin: 0 0 1rem 0;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid #e5e7eb;
}
.modal-body .receive-form .form-section .form-field {
  margin-bottom: 1rem;
}
.modal-body .receive-form .form-section .form-field:last-child {
  margin-bottom: 0;
}
.modal-body .receive-form .form-section .form-field.full-width {
  grid-column: 1/-1;
}
.modal-body .receive-form .form-section .form-field .field-label {
  display: block;
  font-size: 0.875rem;
  font-weight: 500;
  color: #374151;
  margin-bottom: 0.5rem;
}
.modal-body .receive-form .form-section .form-field .form-input,
.modal-body .receive-form .form-section .form-field .form-select,
.modal-body .receive-form .form-section .form-field .form-textarea {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #d1d5db;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  transition: border-color 0.2s;
}
.modal-body .receive-form .form-section .form-field .form-input:focus,
.modal-body .receive-form .form-section .form-field .form-select:focus,
.modal-body .receive-form .form-section .form-field .form-textarea:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}
.modal-body .receive-form .form-section .form-field .form-input.ng-invalid.ng-touched,
.modal-body .receive-form .form-section .form-field .form-select.ng-invalid.ng-touched,
.modal-body .receive-form .form-section .form-field .form-textarea.ng-invalid.ng-touched {
  border-color: #ef4444;
}
.modal-body .receive-form .form-section .form-field .form-textarea {
  resize: vertical;
  min-height: 80px;
}
.modal-body .receive-form .form-section .form-field .error-message {
  color: #ef4444;
  font-size: 0.75rem;
  margin-top: 0.25rem;
  min-height: 1rem;
}
.modal-body .receive-form .form-section .form-row {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1rem;
  margin-bottom: 1rem;
}
.modal-body .receive-form .form-section .form-row:last-child {
  margin-bottom: 0;
}
.modal-body .receive-form .form-section .items-container .item-row {
  border: 1px solid #e5e7eb;
  border-radius: 0.5rem;
  padding: 1rem;
  margin-bottom: 1rem;
  background-color: #f9fafb;
}
.modal-body .receive-form .form-section .items-container .item-row:last-child {
  margin-bottom: 0;
}
.modal-body .receive-form .form-section .items-container .item-row .item-header {
  margin-bottom: 1rem;
}
.modal-body .receive-form .form-section .items-container .item-row .item-header .item-title {
  font-size: 1rem;
  font-weight: 600;
  color: #374151;
  margin: 0 0 0.5rem 0;
}
.modal-body .receive-form .form-section .items-container .item-row .item-header .item-summary {
  display: flex;
  gap: 1rem;
  font-size: 0.875rem;
}
.modal-body .receive-form .form-section .items-container .item-row .item-header .item-summary span {
  padding: 0.25rem 0.5rem;
  border-radius: 0.375rem;
  font-weight: 500;
}
.modal-body .receive-form .form-section .items-container .item-row .item-header .item-summary span.ordered {
  background-color: #dbeafe;
  color: #1d4ed8;
}
.modal-body .receive-form .form-section .items-container .item-row .item-header .item-summary span.received {
  background-color: #dcfce7;
  color: #16a34a;
}
.modal-body .receive-form .form-section .items-container .item-row .item-header .item-summary span.remaining {
  background-color: #fef3c7;
  color: #d97706;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  padding: 1rem 1.5rem 1.5rem 1.5rem;
  border-top: 1px solid #e5e7eb;
}

.btn {
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  font-size: 0.875rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s;
  border: 1px solid transparent;
}
.btn.btn-primary {
  background-color: #3b82f6;
  color: white;
}
.btn.btn-primary:hover {
  background-color: #2563eb;
}
.btn.btn-primary:disabled {
  background-color: #9ca3af;
  cursor: not-allowed;
}
.btn.btn-secondary {
  background-color: #6b7280;
  color: white;
}
.btn.btn-secondary:hover {
  background-color: #4b5563;
}

@media (max-width: 768px) {
  .modal-container {
    margin: 0.5rem;
    max-height: calc(100vh - 1rem);
  }
  .modal-body {
    padding: 1rem;
  }
  .modal-body .po-summary .summary-grid {
    grid-template-columns: 1fr;
    gap: 0.75rem;
  }
  .modal-body .receive-form .form-section .items-container .item-row .item-header .item-summary {
    flex-direction: column;
    gap: 0.5rem;
  }
  .modal-footer {
    flex-direction: column;
    gap: 0.75rem;
  }
  .modal-footer .btn {
    width: 100%;
  }
}
